

.qualification__container {
    max-width: 768px;
    margin: 0 auto;
    padding: 2rem;
}

.qualification__tabs {
    display: flex;
    justify-content: center;
    margin-bottom: var(--mb-2);
}

.qualification__button {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    color: var(--qual-color);
    cursor: pointer;
    margin: 0 1.5rem;
    display: flex;
    align-items: center;
    position: relative;
}

.qualification__button:hover {
    color: var(--hover-color);
}

.qualification__icon {
    font-size: 1.58rem;
    margin-right: var(--mb-2);
}

.qualification_sections {
    display: flex;
    flex-direction: column;
}

.qualification_content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    position: relative;
    padding-left: 2rem;
    padding-right: 2rem;
}

.qualification_data {
    display: flex;
    align-items: flex-start;
    position: relative;
    justify-content: flex-start;
}

.qualification_data:nth-child(even) {
    justify-content: flex-end;
}

.qualification_title_container,
.qualification_subtitle_container,
.qualification_calender_container {
    margin-bottom: var(--mb-2);
}

.qualification_title {
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    color: var(--qual-color);
    margin-bottom: 0.5rem;
}

.qualification_subtitle {
    font-size: 0.875rem;
    color: var(--qual-color);
    margin-top: 0.5rem;
}

.qualification__calender {
    font-size: 0.875rem;
    color: #a1a1a1;
    margin-top: 0.5rem;
}

.qualification_rounder {
    width: 30px;
    height: 30px;
    background-color: var(--qual-color);
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.qualification_info {
    width: 200px;
}

.qualification_line {
    width: 2px;
    height: 100%;
    background-color: var(--qual-color);
    position: absolute;
    left: 50%;
    top: 1.5rem;
    transform: translateX(-50%);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.qualification__tabs {
    padding-bottom: 1rem;
    margin-bottom: 5rem;
}

@media (max-width: 600px) {
    body {
        font-size: calc(1rem + 1vw);
    }
}
