/* Default layout for larger screens */
.about__container {
    display: grid;
    grid-template-columns: 3fr 9fr; /* Default layout for larger screens */
    column-gap: 1.875rem;
    color: black;

    margin: auto; 
    height: fit-content; 
    width: fit-content; 
    justify-content: center; 
    align-items: center; 
    position: relative; /* Needed for some custom alignments */
}

/* For smaller screens (tablets) */
@media (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr; /* Stack content in one column */
        width: 90%; /* Reduce width */
        padding: 1rem; /* Add padding */
    }
}

/* For even smaller screens (mobile) */
@media (max-width: 600px) {
    .about__container {
        width: 95%; /* Reduce the width even more */
        padding: 1rem; /* Add padding */
        grid-template-columns: 1fr; /* Keep content in one column */
    }
}

/* About Data Section */
.about__data {
    padding: 1.875rem;
    background-color: var(--container-color);
    box-shadow: var(--shadow);
    border-radius: var(--border-radius);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1.875rem;
    align-items: flex-start;
    position: relative;
}

/* Triangle arrow on the left of the About Data box */
.about__data::before {
    content: '';
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 15px solid var(--container-color);
    position: absolute;
    left: -0.93rem;
    top: 20%;
}

/* Description styles */
.about__description {
    margin-bottom: 1rem;
    text-align: justify;
}

/* Skills section layout */
.about__skills {
    row-gap: 1.25rem;
}

.skills__titles {
    display: flex;
    justify-content: space-between;
}

.skills__name {
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}

.skills__number {
    line-height: 1.2;
}

.skills__bar,
.skills__percentage {
    height: 7px;
    border-radius: 0.25rem;
}

.skills__bar {
    background-color: gray;
}

.skills__percentage {
    display: block;
}

.development {
    width: 90%;
    background-color: yellow;
}

.ui__design {
    width: 80%;
    background-color: red;
}

.photo {
    width: 90%;
    background-color: green;
}

/* About Boxes Layout */
.about__boxes {
    grid-template-columns: repeat(4, 1fr); /* Default: 4 boxes */
    column-gap: 1.875rem;
    margin: 200px;
}

/* Box Layout */
.about__box {
    display: flex;
    column-gap: 1.5rem;
}

.about__icon {
    font-size: var(--h1-font-size);
}

.about__title {
    font-size: 1.85rem;
}

/* Media Queries for Responsive Design */

/* For tablets or smaller screens (up to 1024px) */
@media (max-width: 1024px) {
    .about__boxes {
        grid-template-columns: repeat(2, 1fr); /* Change to 2 boxes per row on medium screens */
        margin: 100px; /* Reduced margin */
    }

    .about__container {
        grid-template-columns: 1fr; /* Stack content */
        width: 90%; /* Set width to 90% */
        padding: 1rem; /* Padding for spacing */
    }
}

/* For mobile devices (up to 600px) */
@media (max-width: 600px) {
    .about__boxes {
        grid-template-columns: 1fr; /* Stack boxes in one column */
        margin: 20px; /* Reduced margin */
    }

    .about__container {
        width: 95%; /* Further reduce the width */
        padding: 1rem; /* Add padding */
        grid-template-columns: 1fr; /* Keep content in one column */
    }
}
